<template>
  <div>
    <b-row>
      <div class="title-fs-2 ml-5">{{ $t("REDUCTIONPROPOSALMENU") }}</div>
      <br /><br />
    </b-row>
    <b-form class="ml-5">
      <div class="ml-3">
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("REGISTRATIONFEE")
            }}</label>
          </b-col>
          <b-col cols="12" xl="3">
            <b-form-input
              v-model="reductionProposal.registrationFee"
              class="form-control"
            ></b-form-input>
            <show-errors
              :control="$v.reductionProposal.registrationFee"
              :field="$t('REGISTRATIONFEE')"
            />
          </b-col>
          <b-col cols="12" xl="3">
            <b-button
              @click="addExpenseItem"
              :disabled="supplier_reduction_proposal_info_no != null"
              variant="primary"
              class="mt-1"
              >{{ $t("EXPENSEITEMSELECTION") }}</b-button
            >
          </b-col>
        </b-row>
        <br />
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("REDUCTIONPROPOSALMENUNAME")
            }}</label>
          </b-col>
          <b-col cols="12" xl="6">
            <b-form-input
              v-model="reductionProposal.proposalName"
              class="form-control"
              maxlength="100"
            ></b-form-input>
            <show-errors
              :control="$v.reductionProposal.proposalName"
              :field="$t('REDUCTIONPROPOSALMENUNAME')"
            />
          </b-col>
        </b-row>
        <br />
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("PROPOSEDAMOUNT")
            }}</label>
          </b-col>
          <b-col cols="12" xl="3">
            <b-form-input
              v-model="reductionProposal.proposedAmount"
              class="form-control"
              maxlength="9"
              onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
            ></b-form-input>
            <show-errors
              :control="$v.reductionProposal.proposedAmount"
              :field="$t('PROPOSEDAMOUNT')"
            />
          </b-col>
        </b-row>
        <br />
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button
              variant="primary"
              class="px-4 required-btnAny align-content-center"
              >{{ $t("REQUIREDBUTTON") }}</b-button
            >
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("EXPIRATIONDATE")
            }}</label>
          </b-col>
          <b-col cols="12" xl="3">
            <b-input-group>
              <b-form-input
                v-model="reductionProposal.expiryDate_start"
                type="text"
                autocomplete="off"
              ></b-form-input>
              <b-input-group-append>
                <b-form-datepicker
                  v-model="reductionProposal.expiryDate_start"
                  @context="onContext"
                  :date-format-options="{
                    year: 'numeric',
                    month: 'numeric',
                    day: 'numeric',
                  }"
                  locale="ja"
                  button-only
                  right
                ></b-form-datepicker>
              </b-input-group-append>
            </b-input-group>
            <show-errors
              :control="$v.reductionProposal.expiryDate_start"
              :field="$t('EXPIRATIONDATE')"
            />
          </b-col>
          ～
          <b-col cols="12" xl="3">
            <b-input-group>
              <b-form-input
                v-model="reductionProposal.expiryDate_end"
                type="text"
                autocomplete="off"
              ></b-form-input>
              <b-input-group-append>
                <b-form-datepicker
                  v-model="reductionProposal.expiryDate_end"
                  @context="onContext"
                  :date-format-options="{
                    year: 'numeric',
                    month: 'numeric',
                    day: 'numeric',
                  }"
                  locale="ja"
                  button-only
                  right
                ></b-form-datepicker>
              </b-input-group-append>
            </b-input-group>
            <show-errors
              :control="$v.reductionProposal.expiryDate_end"
              :field="$t('EXPIRATIONDATE')"
            />
          </b-col>
        </b-row>
        <br />
        <b-row class="d-flex align-items-center">
          <b-col cols="12" xl="3">
            <b-button variant="outline-primary" class="px-4 required-btnAny">{{
              $t("ANYBUTTON")
            }}</b-button>
            &nbsp;&nbsp;
            <label class="highLightHeaderLabel">{{
              $t("PROPOSALTEMPLATE")
            }}</label>
          </b-col>
          <b-col cols="12" xl="6">
            <b-form-textarea
              v-model="reductionProposal.suggestion"
              class="mb-1"
              rows="5"
            ></b-form-textarea>
          </b-col>
        </b-row>
        <br />
      </div>
      <hr class="hrType" />

      <b-row>
        <div class="title-fs-2">
          {{ $t("REDUCTIONPROPOSALMENUCONDITIONS") }}
        </div>
        <br /><br />
      </b-row>
      <b-row class="ml-2">{{ $t("COMPANYSIZE") }}</b-row>
      <b-row class="ml-2">
        <b-col>
          <b-form-radio-group v-model="proposalCondition.companySize">
            <b-form-radio class="pt-2" value="0">{{
              $t("COMPANYSIZESMALL")
            }}</b-form-radio>
            <b-form-radio class="pt-2" value="1">{{
              $t("COMPANYSIZEMEDIUM")
            }}</b-form-radio>
            <b-form-radio class="pt-2" value="2">{{
              $t("COMPANYSIZELARGE")
            }}</b-form-radio>
          </b-form-radio-group>
        </b-col>

        <b-col xl="7">
          <div class="border border-dark pl-2 pr-2">
            {{ $t("COMPANYSIZEMEMO.LINE1") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE2") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE3") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE4") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE5") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE6") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE7") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE8") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE9") }}<br />
            {{ $t("COMPANYSIZEMEMO.LINE10") }}
          </div>
        </b-col>
      </b-row>
      <br />
      <b-row class="ml-2">{{ $t("INDUSTRY") }}</b-row>
      <b-row class="ml-2">
        <b-col>
          <b-form-radio-group
            v-model="proposalCondition.industry"
            @change="showHideIndustryData"
          >
            <b-form-radio class="pt-2" value="0">{{
              $t("ALLINDUSTRY")
            }}</b-form-radio>
            <b-form-radio class="pt-2" value="1">{{
              $t("INDIVIDUALINDUSTRY")
            }}</b-form-radio>
          </b-form-radio-group>
        </b-col>
        <b-col xl="8"> </b-col>
      </b-row>
      <b-row>
        <b-col xl="6">
          <div class="border border-dark pl-2 pr-2 ml-3">
            <b-form-group>
              <b-form-radio-group
                id="checkbox-group-1"
                v-model="proposalCondition.industries"
                :options="industryOptions"
                class="radioControl mb-3 pt-2"
                size="md"
                stacked
              ></b-form-radio-group>
            </b-form-group>
          </div>
        </b-col>
        <b-col xl="2">
          <b-button
            @click="addAnIndustry"
            :disabled="
              proposalCondition.industry == null ||
              proposalCondition.industry == 0
            "
            variant="primary"
            class="px-4 ml-3 mt-2 align-content-center"
            >{{ $t("ADDINDUSTRY") }}</b-button
          >
        </b-col>
      </b-row>
      <b-row class="d-flex align-items-center">
        <b-col cols="11" xl="6">
          <b-link
            @click="deleteIndustry"
            :disabled="proposalCondition.industries == null"
            href="javascript:;"
            class="card-link float-left link mt-2 ml-3"
          >
            <u>{{ $t("INDUSTRYDELTEXT") }}</u>
          </b-link>
        </b-col>
      </b-row>
      <br />

      <b-row class="ml-2">{{ $t("PROPOSEDAMOUNT") }}</b-row>
      <b-row class="ml-5">{{ $t("REDUCTIONPROPOSAL") }}</b-row>
      <b-row class="d-flex align-items-center ml-4">
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.proposedAmount"
            class="form-control"
            maxlength="3"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="auto">
          {{ $t("DECREASE") }}
        </b-col>
      </b-row>

      <b-row class="ml-2">{{ $t("DASHBOARD.SALES_SCALE") }}</b-row>
      <b-row class="ml-5">{{ $t("SALES_SCALE_RANGE") }}</b-row>
      <b-row class="d-flex align-items-center ml-4">
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.revenue_terms_low"
            class="form-control"
            maxlength="9"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="1">{{ $t("DASHBOARD.COST_UNIT") }} ～ </b-col>
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.revenue_terms_up"
            class="form-control"
            maxlength="9"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="1">{{ $t("DASHBOARD.COST_UNIT") }}</b-col>
        <b-col cols="4"></b-col>
      </b-row>

      <b-row class="ml-2">{{ $t("DASHBOARD.BUSINESS_AREA") }}</b-row>
      <b-row class="ml-5">{{ $t("BUSINESS_AREA_RANGE") }}</b-row>
      <b-row class="d-flex align-items-center ml-4">
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.floor_space_terms_low"
            class="form-control"
            maxlength="5"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="1">{{ $t("DASHBOARD.AREA_UNIT") }} ～ </b-col>
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.floor_space_terms_up"
            class="form-control"
            maxlength="5"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="1">{{ $t("DASHBOARD.AREA_UNIT") }}</b-col>
        <b-col cols="4"></b-col>
      </b-row>

      <b-row class="ml-2">{{ $t("STORE_COUNTS") }}</b-row>
      <b-row class="ml-5">{{ $t("STORE_COUNTS_RANGE") }}</b-row>
      <b-row class="d-flex align-items-center ml-4">
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.number_of_stores_terms_low"
            class="form-control"
            maxlength="5"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="1">{{ $t("STORE") }} ～ </b-col>
        <b-col cols="3">
          <b-form-input
            v-model="proposalCondition.number_of_stores_terms_up"
            class="form-control"
            maxlength="5"
            onkeypress="return (event.charCode !=8 && event.charCode ==0 || (event.charCode >= 48 && event.charCode <= 57))"
          ></b-form-input>
        </b-col>
        <b-col cols="1">{{ $t("STORE") }}</b-col>
        <b-col cols="4"></b-col>
      </b-row>
      <br /><br />
      <b-row>
        <b-col cols="6" xl="3">
          <b-button
            @click="addUpdateReductionProposal"
            variant="primary"
            class="px-4 align-content-center"
            >{{
              supplier_reduction_proposal_info_no == null
                ? $t("REGISTER")
                : $t("UPDATE")
            }}</b-button
          >
        </b-col>
      </b-row>
      <br /><br />
    </b-form>

    <b-modal
      id="tree-modal"
      ref="tree-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("ADDEXPENSEITEM") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <SubMenuTree
              @update-expense-type="updateExpenseItem"
              :tree-options="treeOptions"
              :disabledItems="disabledExpenseItems"
              :treeTitle="addExpenseTitle"
            />
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            :disabled="currentSelectedExpenseItem == ''"
            @click="getExpansionItem"
            variant="primary"
            class="float-right"
            >{{ $t("ADD") }}
            <i v-if="selectLoader" class="fa fa-spinner fa-spin" />
          </b-button>
        </b-col>
      </b-row>
    </b-modal>

    <b-modal
      id="tree-modal1"
      ref="tree-modal1"
      :borderless="true"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("INDUSTRYSELECTION") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <SubMenuTree
              :treeTitle="''"
              @update-expense-type="updateIndustryItem"
              :industry="true"
              :data="treeData"
              :tree-options="treeOptions"
            />
          </b-col>
        </b-row>
      </b-card>
      <br />
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            :disabled="currentSelectedIndustryItem == ''"
            @click="getIndustryName"
            class="float-right"
            variant="primary"
            >{{ $t("SELECTEXPENSEITEMS") }}</b-button
          >
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import SubMenuTree from "@/components/SubMenuTree.vue";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import ShowErrors from "@/components/ShowErrors";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "ReductionProposalAddEdit",
  components: { SubMenuTree, ShowErrors },
  data() {
    return {
      supplier_no: null,
      supplier_reduction_proposal_info_no: null,
      supplier_expense_item_info_no: null,
      reductionProposal: {
        registrationFee: "",
        proposalName: "",
        proposedAmount: "",
        expiryDate_start: "",
        expiryDate_end: "",
        suggestion: "",
      },
      proposalCondition: {
        companySize: null,
        industry: null,
        industries: null,
        proposedAmount: "",
        revenue_terms_low: "",
        revenue_terms_up: "",
        floor_space_terms_low: "",
        floor_space_terms_up: "",
        number_of_stores_terms_low: "",
        number_of_stores_terms_up: "",
      },
      industryOptions: [],
      currentSelectedIndustryItem: "",
      currentSelectedExpenseItem: "",
      backupIndustryOptions: [],
      treeOptions: {
        checkbox: false,
      },
      treeIndustryOptions: {
        checkbox: false,
      },
      treeData: [],
      allTreeData: [],
      allIndustryTreeData: [],
      addExpenseTitle: "",
      disabledExpenseItems: [],
      selectLoader: false,
      dateStart: "",
      dateEnd: "",
      formatted: "",
      selected: "",
    };
  },
  validations: {
    reductionProposal: {
      registrationFee: { required },
      proposalName: { required },
      proposedAmount: { required },
      expiryDate_start: { required },
      expiryDate_end: { required },
      suggestion: "",
    },
  },
  computed: {
    ...mapGetters({
      selected_industry_item: "getSelectedIndustryItem",
      selected_expense_item: "getSelectedExpenseItem",
      user_details: "getUserDetails",
      supplier_data: "getSupplierData",
      reduction_propsal_info: "getSupplierReductionProposalData",
    }),
  },
  created() {
    this.getTreeData();
    this.getIndustryTreeData();
  },
  async mounted() {
    this.$store.dispatch("updateSelectedExpenseItem", {
      accounts_name_cd: "",
      expense_item_cd: "",
      expense_item_nm: "",
    });
    this.supplier_no = this.supplier_data.id;
    this.supplier_reduction_proposal_info_no = this.reduction_propsal_info.id;
    if (this.supplier_reduction_proposal_info_no != null)
      this.getSupplierReductionProposalInfo();
  },
  methods: {
    /**
     * @description function used in the datetime picker control
     * @name onContext
     * @param ctx
     * @returns nothing
     */
    onContext(ctx) {
      this.formatted = ctx.selectedFormatted;
      this.selected = ctx.selectedYMD;
    },
    showHideIndustryData() {
      setTimeout(() => {
        console.log("proposalCondition.ind", this.proposalCondition.industry);
        if (this.proposalCondition.industry == "0") {
          this.industryOptions = [];
          this.proposalCondition.industries = null;
        } else {
          this.industryOptions = this.backupIndustryOptions;
        }
      }, 0);
    },
    /**
     * @description function to get reduction proposal info based on the id
     * @name getSupplierReductionProposalInfo
     * @param none
     * @returns nothing
     */
    async getSupplierReductionProposalInfo() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUPPLIERREADSUPPLIERREDUCTIONPROPOSALINFODETAILS,
        {
          client_no: this.user_details.client_account_no,
          supplier_reduction_proposal_info_no: this
            .supplier_reduction_proposal_info_no,
        }
      );

      if (data.success) {
        let responseData = data.data;
        this.$store.dispatch("updateSelectedExpenseItem", {
          accounts_name_cd:
            responseData.supplierExpenseItemInfoObject[0].accounts_name_cd,
          expense_item_cd:
            responseData.supplierExpenseItemInfoObject[0].expense_item_cd,
          expense_item_nm:
            responseData.supplierExpenseItemInfoObject[0].expense_item_nm,
        });
        this.reductionProposal.registrationFee =
          responseData.supplierExpenseItemInfoObject[0].expense_item_nm;
        this.supplier_expense_item_info_no =
          responseData.supplierReductionProposalInfoObject.supplier_expense_item_info_no;
        this.reductionProposal.proposalName =
          responseData.supplierReductionProposalInfoObject.reduction_proposal_name;
        this.reductionProposal.proposedAmount = Math.floor(
          responseData.supplierReductionProposalInfoObject
            .reduction_proposal_cost
        );
        this.proposalCondition.proposedAmount = Number(
          responseData.supplierReductionProposalInfoObject
            .reduction_proposal_cost_terms
        );
        this.reductionProposal.expiryDate_start =
          responseData.supplierReductionProposalInfoObject.effective_date_st;
        this.reductionProposal.expiryDate_end =
          responseData.supplierReductionProposalInfoObject.effective_date_ed;
        this.proposalCondition.industry =
          responseData.supplierReductionProposalInfoObject.industry_terms_flg ==
          false
            ? 0
            : 1;
        this.proposalCondition.companySize =
          responseData.supplierReductionProposalInfoObject
            .company_size_b_terms_flg == true
            ? 2
            : this.proposalCondition.companySize;
        this.proposalCondition.companySize =
          responseData.supplierReductionProposalInfoObject
            .company_size_m_terms_flg == true
            ? 1
            : this.proposalCondition.companySize;
        this.proposalCondition.companySize =
          responseData.supplierReductionProposalInfoObject
            .company_size_s_terms_flg == true
            ? 0
            : this.proposalCondition.companySize;
        this.proposalCondition.revenue_terms_low = Math.floor(
          responseData.supplierReductionProposalInfoObject.revenue_terms_low
        );
        this.proposalCondition.revenue_terms_up = Math.floor(
          responseData.supplierReductionProposalInfoObject.revenue_terms_up
        );
        this.proposalCondition.floor_space_terms_low = Math.floor(
          responseData.supplierReductionProposalInfoObject.floor_space_terms_low
        );
        this.proposalCondition.floor_space_terms_up = Math.floor(
          responseData.supplierReductionProposalInfoObject.floor_space_terms_up
        );
        this.proposalCondition.number_of_stores_terms_low = Math.floor(
          responseData.supplierReductionProposalInfoObject
            .number_of_stores_terms_low
        );
        this.proposalCondition.number_of_stores_terms_up = Math.floor(
          responseData.supplierReductionProposalInfoObject
            .number_of_stores_terms_up
        );
        this.reductionProposal.suggestion =
          responseData.supplierExpenseItemInfoObject[0].reduction_proposal_article_template;

        if (this.proposalCondition.industry == "1")
          responseData.reductionProposalIndustryListObject.forEach((item) => {
            this.industryOptions.push({
              text: item.industry_nm,
              value: item.industry_no,
            });
          });
        this.backupIndustryOptions = this.industryOptions;
      }
    },
    /**
     * @description function to get the expense item list
     * @name getTreeData
     * @param none
     * @returns nothing
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.READWITHJOINMEXPENSEITEM,
        {
          paginate: false,
          limit: 5000,
          fieldlist: [
            "m_accounts_name.accounts_name_cd",
            "m_accounts_name.accounts_name_nm",
            "m_expense_item.expense_item_cd",
            "m_expense_item.expense_item_nm",
          ],
          conditions: [
            {
              key: "m_accounts_name.use_flg",
              value: true,
            },
            {
              key: "m_expense_item.use_flg",
              value: true,
            },
          ],
          joins: [
            {
              table: "m_accounts_name",
              join_type: "inner",
              join_conditions: [
                {
                  join_using: "m_accounts_name.accounts_name_cd",
                  join_operator: "=",
                  join_with: "m_expense_item.accounts_name_cd",
                },
              ],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data.data;
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
      }
    },
    /**
     * @description fucntion to show the expense item selection modal popup
     * @name addExpenseItem
     * @param none
     * @returns nothing
     */
    addExpenseItem() {
      this.currentSelectedExpenseItem = "";
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: "",
        expense_item_cd: "",
        expense_item_nm: "",
      });
      this.$refs["tree-modal"].show();
    },
    /**
     * @description function to update the store from the selected expense item
     * @name updateExpenseItem
     * @param none
     * @returns nothing
     */
    updateExpenseItem(item) {
      this.selectedExpenseItem = item;
      let treeItem = this._.find(this.allTreeData, { expense_item_nm: item });
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: treeItem.accounts_name_cd,
        expense_item_cd: treeItem.expense_item_cd,
        expense_item_nm: treeItem.expense_item_nm,
      });
      this.selectedTreeItem = treeItem;
      this.currentSelectedExpenseItem = treeItem.expense_item_nm
        ? treeItem.expense_item_nm
        : "";
    },
    /**
     * @description function to get the expense item
     * @name getExpansionItem
     * @param none
     * @returns nothing
     */
    async getExpansionItem() {
      this.selectLoader = true;
      let selectedItem = this.selectedTreeItem;
      this.reductionProposal.registrationFee = this.selected_expense_item.expense_item_nm;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMCOSTEXPANSIONITEM,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "accounts_name_cd",
              value: selectedItem.accounts_name_cd,
            },
            {
              key: "expense_item_cd",
              value: selectedItem.expense_item_cd,
            },
          ],
        }
      );
      let responseData = data.data;
      let y = [];
      responseData.map((item) => {
        y.push({
          name: item ? item.expansion_item_nm : "-",
          value: "",
          id: item ? item.cost_expansion_item_no : "-",
          kbn: item ? item.expansion_item_format_kbn : "-",
        });

        this.supplier_expense_item_info_no = item.cost_expansion_item_no;
      });

      this.selected = {
        expansionItem1: y[0],
        expansionItem2: y[1],
        expansionItem3: y[2],
        expansionItem4: y[3],
        expansionItem5: y[4],
        expansionItem6: y[5],
        account: {
          name: this.$t("INPUTEXPENSE.account"),
          value: selectedItem.accounts_name_nm,
          code: selectedItem.accounts_name_cd,
        },
        expenseItem: {
          name: this.$t("INPUTEXPENSE.expenseItem"),
          value: selectedItem.expense_item_nm,
          code: selectedItem.expense_item_cd,
        },
        expense: {
          name: this.$t("INPUTEXPENSE.expense"),
          value: "",
        },
      };
      let relatedFlag = [];
      for (let i in [1, 2, 3, 4, 5, 6]) {
        let m = `expansionItem${parseInt(i) + 1}`;
        let keyName = this.selected[m];
        if (keyName != undefined && keyName.value != "-") {
          relatedFlag.push(true);
        } else {
          relatedFlag.push(false);
        }
      }
      if (relatedFlag.includes(true)) {
        this.relatedFlagInfo = true;
      } else {
        this.relatedFlagInfo = false;
      }
      this.selectLoader = false;
      this.$refs["tree-modal"].hide();
    },
    /**
     * @description function to show the insdustry modal popup
     * @name addAnIndustry
     * @param none
     * @returns nothing
     */
    addAnIndustry() {
      this.currentSelectedIndustryItem = "";
      this.$refs["tree-modal1"].show();
    },
    /**
     * @description function to get the industry list
     * @name getIndustryTreeData
     * @param none
     * @returns nothing
     */
    async getIndustryTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allIndustryTreeData = responseData;
        let mapData = [];
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let ParentData = [];
          groupedData[key].forEach((item) => {
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              ParentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };
              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + ParentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });

        this.$store.dispatch("updateIndustryTree", mapData);
      }
    },
    /**
     * @description function update the industry item to the store
     * @name updateIndustryItem
     * @param none
     * @returns nothing
     */
    updateIndustryItem(item) {
      let Kbn2Data = [];
      this.allIndustryTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          Kbn2Data.push(item1);
        }
      });

      let treeItem = this._.find(Kbn2Data, { industry_nm: item });
      this.$store.dispatch("updateselectedIndustryItem", {
        industry_nm: treeItem.industry_nm,
        industry_no: treeItem.industry_no,
      });
      this.currentSelectedIndustryItem = treeItem.industry_nm;
    },
    /**
     * @description function to the selected industry and hide the industry modal popup
     * @name getIndustryName
     * @param none
     * @returns nothing
     */
    getIndustryName() {
      this.currentSelectedIndustryItem = this.selected_industry_item.industry_nm;
      let dataExist = this._.find(this.industryOptions, {
        value: this.selected_industry_item.industry_no,
      });
      if (!dataExist) {
        this.industryOptions.push({
          text: this.selected_industry_item.industry_nm,
          value: this.selected_industry_item.industry_no,
        });
      }
      this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to delete the industry which is selected from the list
     * @name deleteIndustry
     * @param none
     * @return nothing
     */
    async deleteIndustry() {
      if (this.supplier_reduction_proposal_info_no != null) {
        if (this.proposalCondition.industries != null) {
          let payload = [];
          payload.push({
            key: "supplier_reduction_proposal_info_no",
            value: this.supplier_reduction_proposal_info_no,
          });
          payload.push({
            key: "industry_no",
            value: [this.proposalCondition.industries],
          });

          const { data } = await BasicInfoApis.postAPI(
            endPoint.MASTERSOFTDELETEMREDUCTIONPROPOSALINDUSRTYINFO,
            {
              conditions: payload,
              update_id: localStorage.getItem("personID"),
            }
          );
          if (data.success) {
            this.industryOptions = this._.filter(
              this.industryOptions,
              (item) => {
                return this.proposalCondition.industries != item.value;
              }
            );
          }
        }
      } else {
        this.industryOptions = this._.filter(this.industryOptions, (item) => {
          return this.proposalCondition.industries != item.value;
        });
      }
    },
    /**
     * @description function to add/update the supplier reduction proposal info
     * @name addUpdateReductionProposal
     * @param none
     * @returns nothing
     */
    async addUpdateReductionProposal() {
      this.$v.reductionProposal.$touch();
      if (!this.$v.reductionProposal.$invalid) {
        let industries = [];
        if (this.industryOptions.length > 0) {
          this.industryOptions.forEach((item) =>
            industries.push({
              industry_no: item.value,
              create_id: this.user_details.client_account_no,
              update_id: this.user_details.client_account_no,
            })
          );
        }
        let payload = {
          client_no: this.user_details.client_account_no,
          supplier_no: this.supplier_no,
          supplier_reduction_proposal_info: {
            supplier_reduction_proposal_info_no: this
              .supplier_reduction_proposal_info_no,
            supplier_expense_item_info_no: this.supplier_expense_item_info_no,
            reduction_proposal_name: this.reductionProposal.proposalName,
            reduction_proposal_cost: this.reductionProposal.proposedAmount,
            reduction_proposal_cost_terms: this.proposalCondition.proposedAmount
              ? this.proposalCondition.proposedAmount
              : 0,
            effective_date_st: this.reductionProposal.expiryDate_start,
            effective_date_ed: this.reductionProposal.expiryDate_end,
            industry_terms_flg:
              this.proposalCondition.industry == null ||
              this.proposalCondition.industry == 0
                ? false
                : true,
            company_size_s_terms_flg:
              this.proposalCondition.companySize == 0 ? true : false,
            company_size_m_terms_flg:
              this.proposalCondition.companySize == 1 ? true : false,
            company_size_b_terms_flg:
              this.proposalCondition.companySize == 2 ? true : false,
            revenue_terms_low: this.proposalCondition.revenue_terms_low
              ? this.proposalCondition.revenue_terms_low
              : 0,
            revenue_terms_up: this.proposalCondition.revenue_terms_up
              ? this.proposalCondition.revenue_terms_up
              : 0,
            floor_space_terms_low: this.proposalCondition.floor_space_terms_low,
            floor_space_terms_up: this.proposalCondition.floor_space_terms_up,
            number_of_stores_terms_low: this.proposalCondition
              .number_of_stores_terms_low,
            number_of_stores_terms_up: this.proposalCondition
              .number_of_stores_terms_up,
            create_id: this.user_details.client_account_no,
            update_id: this.user_details.client_account_no,
          },
          supplier_expense_item_info: {
            supplier_expense_item_info_no: this.supplier_expense_item_info_no,
            supplier_no: this.supplier_no,
            accounts_name_cd: this.selected_expense_item.accounts_name_cd,
            expense_item_cd: this.selected_expense_item.expense_item_cd,
            reduction_proposal_article_template: this.reductionProposal
              .suggestion,
            create_id: this.user_details.client_account_no,
            update_id: this.user_details.client_account_no,
          },
          reduction_proposal_industry_info: industries,
          created_id: this.user_details.client_account_no,
          update_id: this.user_details.client_account_no,
        };

        const { data } = await BasicInfoApis.postAPI(
          endPoint.SUPPLIERCREATEORUPDATESUPPLIERREDUCTIONPROPOSALINFO,
          payload
        );
        if (data.success) {
          this.$router.push({ name: "supplier_addEdit" });
        }
      }
    },
  },
};
</script>

